'use client';

import Link from 'next/link';
import { useEffect, useState } from 'react';
import { supabase } from '@/lib/supabase/client';

type Company = {
  id: number;
  company_name: string;
  created_at: string;
  user: string;
};

type Profile = {
  user: string;
  full_name: string | null;
};

export default function ManagerDashboard() {
  const [companies, setCompanies] = useState<Company[]>([]);
  const [profiles, setProfiles] = useState<Record<string, string>>({});
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetchData();
  }, []);

  const fetchData = async () => {
    setLoading(true);

    // 1️⃣ Fetch companies
    const { data: companiesData, error: companiesError } =
      await supabase
        .from('companies')
        .select('id, company_name, created_at, user')
        .order('created_at', { ascending: false });

    if (companiesError) {
      console.error('Companies error:', companiesError);
      setLoading(false);
      return;
    }

    // 2️⃣ Fetch profiles
    const { data: profilesData, error: profilesError } =
      await supabase
        .from('profiles')
        .select('user, full_name');

    if (profilesError) {
      console.error('Profiles error:', profilesError);
      setLoading(false);
      return;
    }

    // 3️⃣ Map profiles by user id
    const profileMap: Record<string, string> = {};
    (profilesData as Profile[]).forEach((p) => {
      profileMap[p.user] = p.full_name ?? '—';
    });

    setCompanies(companiesData || []);
    setProfiles(profileMap);
    setLoading(false);
  };

  return (
    <div className="min-h-screen bg-gray-100">
      {/* Top Bar */}
      <header className="bg-black text-yellow-400 px-6 py-4 flex justify-between items-center">
        <h1 className="text-xl font-semibold">Manager Portal</h1>

        <Link
          href="/manager/clients/new"
          className="bg-yellow-400 text-black px-4 py-2 rounded-md text-sm font-medium hover:bg-yellow-300"
        >
          + Add Client
        </Link>
      </header>

      {/* Content */}
      <main className="p-6 max-w-7xl mx-auto">
        <h2 className="text-lg font-semibold mb-4">Companies</h2>

        <div className="bg-white rounded-lg shadow border overflow-x-auto">
          <table className="w-full text-sm">
            <thead className="bg-gray-50 border-b">
              <tr>
                <th className="px-4 py-3 text-left">Company</th>
                <th className="px-4 py-3 text-left">Client Name</th>
                <th className="px-4 py-3 text-left">Created</th>
                <th className="px-4 py-3 text-right">Action</th>
              </tr>
            </thead>

            <tbody>
              {companies.map((company) => (
                <tr
                  key={company.id}
                  className="border-t hover:bg-gray-50"
                >
                  <td className="px-4 py-3 font-medium">
                    {company.company_name}
                  </td>

                  <td className="px-4 py-3 text-gray-700">
                    {profiles[company.user] || '—'}
                  </td>

                  <td className="px-4 py-3 text-gray-500">
                    {new Date(company.created_at).toLocaleDateString()}
                  </td>

                  <td className="px-4 py-3 text-right">
                    <Link
                      href={`/manager/companies/${company.id}/edit`}
                      className="text-sm font-medium text-yellow-600 hover:underline"
                    >
                      Update Context
                    </Link>
                  </td>
                </tr>
              ))}

              {!loading && companies.length === 0 && (
                <tr>
                  <td
                    colSpan={4}
                    className="px-4 py-6 text-center text-gray-500"
                  >
                    No companies found
                  </td>
                </tr>
              )}

              {loading && (
                <tr>
                  <td
                    colSpan={4}
                    className="px-4 py-6 text-center text-gray-400"
                  >
                    Loading companies...
                  </td>
                </tr>
              )}
            </tbody>
          </table>
        </div>
      </main>
    </div>
  );
}
